import React, { useState } from 'react';
import { useFormik } from 'formik';
import Grid from '@material-ui/core/Grid';
import { useSnackbar } from 'notistack';

import PaymentCardForm from '../components/PaymentCardForm';
import EshopAPI from '../EshopAPI';
import { useTranslation } from '../providers/TranslationProvider';

function PaymentCardFormContainer(props) {
  const { redirectToCardList } = props;

  const { t } = useTranslation();
  const { enqueueSnackbar } = useSnackbar();

  const [loading, setLoading] = useState(false);

  const formik = useFormik({
    initialValues: {
      name: '',
      cardNumber: '',
      expiry: '',
      cvc: '',
    },
    validate: (values) => {
      const errors = {};

      if (!values.name) {
        errors.name = `${t('Name')} ${t('Required')}`;
      }

      if (!values.cardNumber) {
        errors.cardNumber = `${t('Card number')} ${t('Required')}`;
      } else if (values.cardNumber.length < 16) {
        errors.cardNumber = `${t('Invalid card number')}`;
      }

      if (!values.expiry) {
        errors.expiry = `${t('Expiry')} ${t('Required')}`;
      } else if (values.expiry.length < 4) {
        errors.expiry = `${t('Invalid expiry')}`;
      }

      if (!values.cvc) {
        errors.cvc = `${t('CVC')} ${t('Required')}`;
      } else if (values.cvc.length < 3) {
        errors.cvc = `${t('Invalid cvc')}`;
      }

      return errors;
    },
    onSubmit: async (values) => {
      setLoading(true);
      EshopAPI.addPaymentCard(values)
        .then(() => {
          setLoading(false);
          redirectToCardList();
        })
        .catch((error) => {
          enqueueSnackbar(
            t('Error') + `${error.message ? ` :  ${error.message}` : ''}`,
            {
              variant: 'error',
            }
          );
          setLoading(false);
        });
    },
  });

  return (
    <Grid container>
      <Grid item xs={12} md={3}></Grid>
      <Grid item xs={12} md={6}>
        <PaymentCardForm
          values={formik.values}
          errors={formik.errors}
          disabled={loading}
          onChange={formik.handleChange}
          onSubmit={formik.handleSubmit}
        />
      </Grid>
      <Grid item xs={12} md={3}></Grid>
    </Grid>
  );
}

export default PaymentCardFormContainer;
